import React from 'react'
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import CollapsableFilter from "./CollapsableFilter";
import { Context } from '../Context';
import styles from '../Assets/Quick_start_style.js'

const homeFilters = window.props.homeFilters
const translations = window.props.translations

function SideBar({classes}){
    return(
        <Context.Consumer>
            {({filters, resetFilters})=>(
                <Paper className={classes.sideBarWrapper}>
                <div className={classes.sideBarTitleWrapper}>
                    <div className={classes.resetWrapper}>
                        <button className={classes.buttonReset} onClick={()=>resetFilters()}>{translations.reset || "Reset"}</button>
                    </div> 
                    <p className={classes.sideBarTitle}>{translations.addFilter || "Add Filter"}</p>
                    <p>{translations.selectSearchFilters || "Select filters you want for your search"}</p>
                </div>
                <div>
                    {homeFilters.map((filterName, index)=>{
                        return (<CollapsableFilter key={index} filter={window.props.filters.find(el=>el.name===filterName)}/>)
                    })}
                </div>
                </Paper>
            )}
        </Context.Consumer>
    )

}

export default withStyles(styles)(SideBar)

