
import React, {Components} from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';


const translations = window.props.translations

const TextAreaInput = ({field, value, addCourseDetails, classes})=>{
    const handleChange =  event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
    return(
        <>
            <TextField rows="4" className={classes.textEditorWrapper} multiline variant="outlined" onChange={handleChange} value={value}/>
        </>
    )
}

export default withStyles(styles)(TextAreaInput)


