import React from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import NumberFormat from 'react-number-format';

const TimeInput = withStyles(styles)(({field, translations,classes, addCourseDetails, value})=>{
    const handleFieldChange=(name, newValue)=>{
        name==="hours"
        ? addCourseDetails({name:field.name, value:[newValue, value[1]]})
        : addCourseDetails({name:field.name, value:[value[0], newValue]})
    }
    return(
        <>
        <div className={[classes.fieldWrapper,classes.smallWrapper].join(' ')}>
        {!field.hideTitle &&
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+"_title"]}</h3>
        }
            <p>{translations.stepFields[field.name+"_helper"]}</p>
            <Grid container>
                <Grid item xs={6}>
                    <NumberFormat value={value[0]} onChange={e=>handleFieldChange("hours", e.target.value)} className={classes.timeInput}/>
                    <span className={classes.inputLabel}>{translations.hours}</span>
                </Grid>
                <Grid item xs={6}>
                    <NumberFormat value={value[1]}  onChange={e=>handleFieldChange("minutes", e.target.value)} className={classes.timeInput}/>
                    <span className={classes.inputLabel}>{translations.minutes}</span>
                </Grid>
            </Grid>

        </div>
        </>
    )
})

export default withStyles(styles)(TimeInput)
