
import React, {Components} from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import FormHelperText from '@material-ui/core/FormHelperText';

const translations = window.props.translations

const TextInput = ({field, defaultValue, handleFieldChange, fixedField, classes, newCourse})=>{
    const wrongId = ['course_session',"course_number"].includes(field.name) && !newCourse.validCourseId
    return(
        <>
            <TextField
                defaultValue={translations.stepFields[field.name+"_default_value"]}
                margin="normal"
                value={defaultValue}
                InputProps={fixedField ? {readOnly:true} : {}}
                onChange={e=>handleFieldChange(field.name, e.target.value)}
                variant="outlined"
                error={wrongId} 
            />
            {wrongId &&
                <FormHelperText error id="component-error-text">{translations.invalidCourseId} </FormHelperText>
            }
        </>
    )
}

export default withStyles(styles)(TextInput)


