import React from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

const translations=window.props.translations

const SelectInput =withStyles(styles)( ({value='', field, classes, addCourseDetails, fixedField})=>{
    const handleChange =  event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
      return(
            <FormControl 
                variant="outlined"
                disabled={fixedField}    
            >

            <Select
            value={value}
            onChange={handleChange}
            inputProps={{
                name: field.name,
            }}
            className={classes.selectField}
            >
            <MenuItem value="">
                <em></em>
            </MenuItem>
            {field.options.map((option, key)=><MenuItem key={key} value={option}>{translations.stepFields[option+"_title"] || option}</MenuItem>)}
            </Select>
        </FormControl>
      )
  })

  export default withStyles(styles)(SelectInput)