import React, { Component, Fragment } from 'react'
import { withStyles } from '@material-ui/styles'
import HeaderNav from '../Components/HeaderNav'
import {Provider} from '../Context'
import SelectMode from '../Components/CourseWizard/SelectMode'
import SelectCourse from '../Components/CourseWizard/SelectCourse'
import styles from '../Assets/Quick_start_style.js'


const newCourseBasis=window.props.courseBasis
const translations = window.props.translations


class SelectModePage extends Component{
    state={
        rerunDisplay : false
    }

    setModeRerun=()=>{
        console.log("hello")
        this.setState({rerunDisplay:true})
    }

    render(){
        const {classes} = this.props
        const {rerunDisplay}=this.state
        return(
            <>
            <HeaderNav/>
            {rerunDisplay
                ?<SelectCourse/>
                :<SelectMode setModeRerun={this.setModeRerun}/>
            }
            </>
        )
    }
}




export default withStyles(styles)(SelectModePage);