import React, { Component } from 'react'
import Typography from '@material-ui/core/Typography';
import { withContext } from '../Context';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { Link } from "react-router-dom";
import styles from '../Assets/Quick_start_style.js'

const translations = window.props.translations

const TabContainer =withStyles(styles)(({title, resultList, classes, page})=>{
    return (
      <div className={classes.searchWrapper}>
        <Typography component="div" className={classes.searchTitle}>
          {title}
        </Typography>
        {resultList!==undefined && resultList.map((course, index)=><ListElement key={index} course={course} buttons={course.type==="vodeclic"?vodeclicButtons : phileasButtons}/>)}
        {(page!=="base" && resultList!==undefined && resultList.length===0 ) && <p>No results</p>}
      </div>


    );
  })

const getButtonsClasses =(index, length, classes)=>{
  let buttonClasses=[classes.actionWrapper,classes.actionBase]
  index===0 && buttonClasses.push(classes.actionFirst)
  index+1===length && buttonClasses.push(classes.actionLast)
  index%2===0 ? buttonClasses.push(classes.actionLight) : buttonClasses.push(classes.actionDark)
  return buttonClasses
}


const phileasButtons=["email","statistics","rerun","configure","contribute","preview"]
const vodeclicButtons=[...phileasButtons].filter(el=>el!=="rerun")

const ListElement=withStyles(styles)(({course, classes, buttons})=>{
  return(
    <div className={classes.resultWrapper}>
      <Paper className={classes.resultCard}>
        <Grid container>
          <Grid item xs={9} className={classes.resultInfos}>
              <h2 className={classes.resultTitle}>{course.course_name}</h2>
              <p style={{textTransform:"capitalize"}}>{course.org}</p>
              <p>{course.course_id}</p>
          </Grid>
          <Grid item xs={3}>
            {
              buttons.map((buttonName,index)=>
                  <div key={index} className={getButtonsClasses(index, buttons.length, classes).join(' ')}>
                    <a style={{color:"white"}} href={course[buttonName+"_url"]}>
                      <span>{translations[buttonName] || buttonName}</span>
                    </a>
                  </div>
              )
            }
          </Grid>
        </Grid>
      </Paper>
    </div>
  )
})

class SearchContent extends Component{
  respectFilters=(course, selectedFilters)=>{
    let results=[]
    Object.entries(selectedFilters).forEach((filter)=>{
      let is_valid=false
      const filterName=filter[0]
      const filterValue=filter[1]

      if(filterValue.includes('all')){
        is_valid=true
      }
      else if (Array.isArray(course[filterName])){
        course[filterName].forEach((courseValue)=>{
          if(filterValue.includes(courseValue)){
            is_valid=true
          }
        })
      }
      else{
        if(filterValue.includes(course[filterName])){
          is_valid=true
        } 
      }
      if(!window.props.orgs.includes(course.org)){
        is_valid=false
      }
      results.push(is_valid)
    })
    return !results.includes(false)
  }

  filterResults=(courses, page, selectedFilters)=>{
    if(page===0){
      return courses.filter((course)=>(course.type==="phileas" && this.respectFilters(course, selectedFilters)))
    }
    else if (page===1){
      return courses.filter((course)=>(course.type==="vodeclic" && this.respectFilters(course, selectedFilters)))
    }
    else{
      return []
    }
  }
    render(){
      const {page, selectedFilters}=this.props
      const results=this.filterResults(window.props.courses, page, selectedFilters)
      return(
          <div>
              {Object.entries(selectedFilters).length === 0 && <TabContainer page={"base"} title={"Choose filters and start research to find all the courses"}></TabContainer>}
              {(Object.entries(selectedFilters).length>0 && page === 0) && <TabContainer title={"Courses results"} resultList={results} page={0}></TabContainer>}
              {(Object.entries(selectedFilters).length>0 && page === 1) && <TabContainer title={"Vodeclic results"} resultList={results} page={1}></TabContainer>}
          </div>
      )
    }
}

export default withContext(SearchContent)