import React, {Components} from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';

const translations = window.props.translations

const CreateFieldWrapper =({children , extraclasses, classes, field})=>{
    return(
        <div className={extraclasses ? classes.fieldWrapper+' '+extraclasses.join(' ') : classes.fieldWrapper}>
            {!field.hideTitle && <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+"_title"]}</h3>}
            <p>{translations.stepFields[field.name+"_helper"]}</p>
            <div>
                {children}
            </div>
        </div>
    )
}

export default withStyles(styles)(CreateFieldWrapper)