import React, {Component} from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import {DropzoneArea} from 'material-ui-dropzone';
import Button from '@material-ui/core/Button';

const translations = window.props.translations


class MultipleUploadsInput extends Component{
    //filesUploadedCount is used to reload dropzone component and erase file
    state={
        currentTitle:undefined,
        currentFile:undefined,
        errorMessage:undefined,
        filesUploadedCount:0
    }

    
    handleFieldChange=(value, name)=>{
        this.setState({[name]:value})
    }

    addFile=()=>{
        if(this.state.currentTitle===undefined){
            this.setState({errorMessage:(translations.missingTitle || "Title is missing")})
        }
        else if(this.state.currentFile===undefined){
            this.setState({errorMessage:(translations.missingFile || "File is missing")})
        }
        else{
            this.setState(prevState=>({
                currentTitle:undefined,
                currentFile:undefined,
                errorMessage:undefined,
                filesUploadedCount:prevState.filesUploadedCount+1
            }))
            this.props.addCourseDetails(
                {name:this.props.field.name,
                    value:[
                        ...this.props.value, 
                        {
                            title:this.state.currentTitle, 
                            file:this.state.currentFile[0]}
                    ]
                }
            )
        }
    }

    eraseFile=(key)=>{
        this.props.addCourseDetails(
            {name:this.props.field.name,
                value:[
                    ...this.props.value.slice(0,key),
                    ...this.props.value.slice(key+1) 
                ]
            }
        )
    }

    render(){
        const {value, classes, editMode}=this.props
        const handleFieldChange=this.handleFieldChange
        return(
            <>
            <div>
                    {
                        editMode==="configure" 
                        ? <p style={{fontStyle:"italic",fontWeight:"bold"}}>Feature not available in configure mode.</p>
                        :                 <>
                        <p>{translations.filteTitle || "File title"}</p>
                            <TextField variant="outlined"
                                value={this.state.currentTitle || ""}
                                onChange={e=>handleFieldChange(e.target.value,"currentTitle")}
                            />
                            <p>{translations.filteTitle || "File to upload"}</p>
                            <div  className="dropzone">
                            <DropzoneArea 
                                filesLimit={1}
                                acceptedFiles={['image/*', 'video/*', 'application/*']}
                                dropzoneText={translations.dragAndDropInfo}
                                showAlerts={false}
                                showPreviewsInDropzone={false}
                                showFileNamesInPreview={false}
                                onChange={e=>handleFieldChange(e,"currentFile")}
                                key={this.state.filesUploadedCount}
                            />
                            {this.state.currentFile && 
                                <p 
                                    style={{cursor:"pointer"}} 
                                    onClick={e=>this.setState(prevState=>({currentFile:undefined, filesUploadedCount:prevState.filesUploadedCount+1 }))}
                                >
                                {this.state.currentFile[0]['name']} X
                                </p>
                            }
                            </div>
        
                        <Button 
                            style={{marginTop:10}} 
                            onClick={e=>this.addFile()}
                        >
                            {translations.addFile || "Add File"}
                        </Button>
                        <p style={{color:"red",fontStyle:"italic"}}>{this.state.errorMessage}</p>
                        <div>
                        <p>{translations.filesUploaded || "Files Uploaded"}</p>
                            {
                                value.length>0 
                                ? 
                                <ul>
                                {
                                    value.map((file, key)=>
                                        <li 
                                        key={key} 
                                        onClick={e=>this.eraseFile(key)} 
                                        style={{cursor:"pointer",padding:10}}>
                                        {file.title} X
                                        </li> 
                                    )
                                }
                                </ul>
                                :
                                <p style={{fontStyle:"italic"}}>{translations.noFiles || "No file uploaded yet"}</p>
                            }
                        </div>
                        </>
                    }
            </div>
            </>
        )
    }
}

export default withStyles(styles)(MultipleUploadsInput)
