import React from 'react'
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/styles';
import styles from '../../Assets/Quick_start_style.js';
import { Link } from "react-router-dom";

const CourseElement=withStyles(styles)(({classes, course, setCourse})=>{
    return(
        <Paper className={classes.courseElementWrapper}>
            <Grid container
            >
                <Grid item xs={9} className={classes.courseElementInfo}>
                    <Typography className={classes.courseElementtitle}>
                        {course.course_name}
                    </Typography>
                    <Typography>
                        {course.course_id}
                    </Typography>
                </Grid>
                <Grid item xs={3} className={classes.flexCenter}>
                <Link to={'/create/'+course.course_id}  style={{height:"100%"}}>
                    <Button className={classes.choseBtn}>
                        <Typography>
                            Choose this course
                        </Typography>
                    </Button>
                </Link>
                </Grid>
            </Grid>
        </Paper>
    )
})

const SelectCourse =({classes})=>{
    const courses =window.props.courses.filter(el=>window.props.orgs.includes(el.org))
    return(
        <div>
            <h1 className={classes.stepTitle}>Course-Rerun</h1>
            <p className={classes.stepHelper}>Select a Course :</p>
            <div className={classes.courseListWrapper}>
                {courses.map((course, key)=><CourseElement course={course} key={key}/>)}
            </div>
        </div>
    )
}
export default withStyles(styles)(SelectCourse)