import React, {Components} from 'react';
import styles from '../../Assets/Quick_start_style.js';
import { withStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import FormHelperText from '@material-ui/core/FormHelperText';
import TextField from '@material-ui/core/TextField';
import Clear from '@material-ui/icons/Clear';
import AddCircleOutline from '@material-ui/icons/AddCircleOutline';

const translations = window.props.translations

const JsonEditor = withStyles(styles)(({classes, value, field, addCourseDetails})=>{

    const handleChange =(newValue, sectionIndex, subsectionIndex=undefined)=>{
        let finalValue = [...value]
        if(subsectionIndex!==undefined){
            finalValue[sectionIndex]['subsections'][subsectionIndex]=newValue
        }
        else{
            finalValue[sectionIndex]["title"]=newValue
        }
        addCourseDetails({name:field.name, value:finalValue})
    }

    const addSection = ()=>{
        addCourseDetails({name:field.name, value:[...value, {"title":translations.new_section || "New Section", "subsections":[translations.new_subsection || "New Subsection"]}]})
    }

    const addSubSection = (sectionIndex)=>{
        let modifiedValue = [...value]
        modifiedValue[sectionIndex]['subsections'].push(translations.new_subsection || "New subsection")  
        addCourseDetails({name:field.name, value:modifiedValue})
    }

    const deleteElement=(sectionIndex, subsectionIndex=undefined)=>{
        let modifiedValue=[...value]
        if(subsectionIndex!==undefined){
            if(modifiedValue[sectionIndex]["subsections"].length>1){
                modifiedValue[sectionIndex]["subsections"]=[...modifiedValue[sectionIndex]["subsections"].splice(0, subsectionIndex), ...modifiedValue[sectionIndex]["subsections"].splice(subsectionIndex+1)]
            }
            else{
                modifiedValue[sectionIndex]["subsections"]=[]
            }
        }
        else{
            modifiedValue= [...modifiedValue.splice(0, sectionIndex), ...modifiedValue.splice(sectionIndex+1)]
        }
        addCourseDetails({name:field.name, value:modifiedValue})
    }

    return(
        <div>
        {value.map((mapElement, key1)=>{
            return(
                <div  key={key1}>
                    <div>
                        <TextField value={mapElement['title']} variant="outlined" onChange={(e)=>handleChange(e.target.value, key1)}/>
                        <Clear className={classes.clear} onClick={()=>deleteElement(key1)}/>
                    </div>
                    {mapElement['subsections'].map((subsection, key2)=> 
                        <div key={key2}  className={classes.subsections} >
                            <TextField value={subsection} variant="outlined" onChange={(e)=>handleChange(e.target.value, key1, key2)}/>
                            <Clear className={classes.clear} onClick={()=>deleteElement(key1, key2)}/>
                        </div>)}
                    <span className={classes.sectionsAdd} onClick={(e)=>addSubSection(key1)} >
                        <AddCircleOutline/> {translations.addSubsection || "Add Subsection"}
                    </span>
                </div>   
                ) 
        })}
        <span className={classes.sectionsAdd} onClick={()=>addSection()} ><AddCircleOutline/> {translations.addSection || "Add Section"}</span>
        </div>
    )
})

export default withStyles(styles)(JsonEditor)